<template>
  <div class="banner">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <router-link :to="{name:'bannerdetail',params:{id:'riverside'}}"><img src="@/assets/img/1.jpg" alt=""></router-link>
        </div>
        <div class="swiper-slide">
          <router-link :to="{name:'bannerdetail',params:{id:'centurysquare'}}"><img src="@/assets/img/2.jpg" alt=""></router-link>
        </div>
        <div class="swiper-slide">
          <router-link :to="{name:'bannerdetail',params:{id:'lotuspond'}}"><img src="@/assets/img/3.jpg" alt=""></router-link>
        </div>
        <div class="swiper-slide">
          <router-link :to="{name:'bannerdetail',params:{id:'fengshan'}}"><img src="@/assets/img/4.jpg" alt=""></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'Swiper'
  export default {
    mounted(){
      var mySwiper = new Swiper('.swiper-container', {
        autoplay: true,//
        grabCursor:true,
        direction:'horizontal',
        loop:true
      })
    }
  }
</script>

<style scoped>
.banner{
  width:100%;
  height:100%;
  box-sizing: content-box;
}
.banner .swiper-container{
  width: 100%;
}
.banner .swiper-wrapper{
  width:600%;
  display: flex;
}
.banner .swiper-wrapper .swiper-slide{
  width:100%;
  height:20rem;
}
.banner img{
  width:100%;
  height:100%;
}
</style>


