<template>
  <div class="nav">
    <a v-for="(item,index) in this.navdata" :key="index">
      <router-link :to="{name:'navdetail',params:{navid:item.canshu}}">
        <img :src="item.icon" alt="">
        <span>{{item.text}}</span>
      </router-link>
    </a>
  </div>
</template>

<script>
  import axios from 'axios';
  import mock from '@/mock'
  export default {
    data(){
      return {
        navdata:''
      }
    },
    mounted() {
      this.getNav()
    },
    methods:{
      getNav(){
        axios.get('/api/getnav').then(res=>{
          this.navdata = res.data;
        })
      }
    },
    watcH:{
      getNav(newVal){
        console.log(newVal)
      }
    }
  }
</script>

<style scoped>
.nav{
  margin-top: 1rem;
  display:flex;
  justify-content: space-around;
  align-items: center;
}
.nav a{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.nav a img{
  width: 5rem;
  height:5rem;
}
.nav a span{
  font-size:1.4rem;
  padding-top:.5rem;
  color:rgb(245, 9, 213);
  font-weight: bold;
}
</style>


